@import url(https://fonts.googleapis.com/css?family=Spartan:400,700,900&display=swap);
body{margin:0;font-family:'Spartan', sans-serif;overflow-x:hidden}.bg-wrapper{position:absolute;overflow:hidden;height:150vh;width:100vw;z-index:-1}.bg{position:absolute;top:0px;left:0px;height:150%;width:150%;z-index:-1;background-image:url(/static/media/bg.e3e91e8c.jpg);background-size:cover;background-position-x:0px;animation:bgAnimation 25s ease alternate-reverse;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}@-webkit-keyframes bgAnimation{from{left:0px;top:0px}to{left:-150px;top:-50px}}@keyframes bgAnimation{from{left:0px;top:0px}to{left:-150px;top:-50px}}

.App{display:flex;flex-direction:column;align-items:center;min-height:100vh;font-size:23px;overflow:hidden}

.table{overflow-x:hidden;width:800px;max-width:85%;margin:20px}table{width:100%;text-align:center;border-collapse:collapse}thead{background-image:-webkit-gradient(linear, left top, left bottom, from(black),color-stop(#232323),color-stop(#3a3737),color-stop(#0b1514),to(rgba(21,82,76,0.701961)));background-image:linear-gradient(to bottom, black,#232323,#3a3737,#0b1514,rgba(21,82,76,0.701961));color:white;mix-blend-mode:luminosity;text-shadow:0px 0px 5px #928888}tbody{color:black;text-shadow:0px 0px 5px #ccc5c5;font-weight:700}thead th{height:40px}tbody tr{background-image:-webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0.239216)),to(rgba(218,218,218,0.560784)));background-image:linear-gradient(to right, rgba(255,255,255,0.239216),rgba(218,218,218,0.560784));-webkit-animation:showTr 1s ease-in;animation:showTr 1s ease-in;height:50px;border-bottom:2px solid #5f5f5f;-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:scaleY(1);transform:scaleY(1);-webkit-transition:-webkit-transform .35s;transition:-webkit-transform .35s;transition:transform .35s;transition:transform .35s, -webkit-transform .35s;font-size:1.3rem}thead th td,tbody tr td{padding:10px}@media only screen and (max-width: 600px){.table{overflow-x:scroll}}@-webkit-keyframes showTr{0%{opacity:0;border-bottom:2px solid transparent;-webkit-transform:scaleY(0);transform:scaleY(0)}50%{-webkit-transform:scaleY(1);transform:scaleY(1)}100%{opacity:1;border-bottom:2px solid #5f5f5f}}@keyframes showTr{0%{opacity:0;border-bottom:2px solid transparent;-webkit-transform:scaleY(0);transform:scaleY(0)}50%{-webkit-transform:scaleY(1);transform:scaleY(1)}100%{opacity:1;border-bottom:2px solid #5f5f5f}}

.form-wrapper{border:1px solid transparent;width:450px;padding:5px;max-width:85%;margin-top:calc(100vw - 95%);max-height:75px;-webkit-transition:all 1s;transition:all 1s;box-sizing:border-box;-webkit-transition-timing-function:cubic-bezier(0.15, 0.6, 0.56, 1.21);transition-timing-function:cubic-bezier(0.15, 0.6, 0.56, 1.21);height:100%}.form-wrapper:focus-within,.form-wrapper:hover{max-height:500px;-webkit-transform:rotateX(0deg);transform:rotateX(0deg);border:1px solid rgba(20,114,105,0.54902);background-color:rgba(20,114,105,0.25098)}.form-wrapper:focus-within .novoUsuario-btn,.form-wrapper:hover .novoUsuario-btn{background-color:rgba(20,114,105,0.54902)}.form-wrapper:focus-within .inner-form-wrapper,.form-wrapper:hover .inner-form-wrapper{opacity:1;overflow:visible;max-height:100%;-webkit-transform:rotateX(0deg);transform:rotateX(0deg)}.novoUsuario-btn{text-align:center;color:#fffcd2;background-color:rgba(20,114,105,0.972549);padding:5px;-webkit-transition:background-color 1s;transition:background-color 1s}.novoUsuario-btn p{margin:10px;cursor:pointer}.inner-form-wrapper{opacity:0;overflow:hidden;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;-webkit-transform:rotateX(-90deg);transform:rotateX(-90deg);-webkit-perspective-origin:top;perspective-origin:top;-webkit-transform-origin:top;transform-origin:top;-webkit-transition:all 1.3s, opacity 1s ease-in;transition:all 1.3s, opacity 1s ease-in;-webkit-transition-timing-function:cubic-bezier(0.15, 0.6, 0.6, 1.47);transition-timing-function:cubic-bezier(0.15, 0.6, 0.6, 1.47)}form{display:flex;flex-direction:column;color:white;justify-content:space-evenly}label{margin:15px 0 5px 0}input{background:-webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,0.2)), to(rgba(0,0,0,0.341176)));background:linear-gradient(to top, rgba(255,255,255,0.2), rgba(0,0,0,0.341176));border:1px solid transparent;color:white;line-height:32px;padding:0 5px;font-size:20px;font-family:'Spartan', sans-serif;cursor:pointer}input:focus-within{outline:2px solid #1472698a;background-color:aquamarine}input:hover{background:-webkit-gradient(linear, left bottom, left top, from(rgba(71,152,144,0.788235)), to(rgba(19,112,103,0.368627)));background:linear-gradient(to top, rgba(71,152,144,0.788235), rgba(19,112,103,0.368627))}.buttons{position:relative;height:50px;margin-top:20px}.buttons:hover input[type="submit"]:nth-child(1){opacity:0}.buttons:hover input[type="submit"]:nth-child(2){opacity:1;font-size:1.5rem}input[type="submit"]{padding:7px;font-family:'Spartan', sans-serif;font-size:23px;-webkit-transition:all ease .35s;transition:all ease .35s;position:absolute;width:100%;-webkit-transition:opacity .45s;transition:opacity .45s}input[type="submit"]:nth-child(1){background:radial-gradient(rgba(255,255,255,0), #124b45);color:#fff1aa;opacity:1}input[type="submit"]:nth-child(2){background:radial-gradient(rgba(255,255,255,0), #124b45, rgba(71,152,144,0.788235));color:white;opacity:0}

